.visual {
	height:600px;
	overflow:hidden;
	position:relative;

	.l_visual {
		
		height:600px;

		> li {
			display:inline-block;
			width:100%;
		}

		@for $i from 1 through 2 {
			.visual#{$i} {
				height:600px;
				background:url(../../images/banner/banner#{$i}.png) center 0;
			}
		}
	}
}
